import React, {MouseEvent, useState, useRef, useEffect} from "react";
import {useNavigate} from "react-router-dom";
import * as S from "./style";
import {MainContainer} from "../main/style";
import MainWhiteBox from "../main/items/MainWhiteBox";
import QnaItem from "../qna/QnaItem";
import LoadingPage from "../loading/Loading";
// @ts-ignore
import qnaData from "../../db/qnaList";
import {off} from "process";

// export interface qnaProps {
//   data: {
//       id: number;
//       q: string;
//       a: object;
//       answer: string;
//       type: string;
//     }
// }

export let count = [0, 0, 0];

const QnaPage = () => {
  // const [qna, setQna]: any = useState([]);
  const [q, setQ] = useState<number>(0);
  const [userClass, setUserClass] = useState<string[]>([]); //    state
  const navigate = useNavigate();
  const [currentSlide, setCurrentSlide] = useState<number>(1);
  const slideRef = useRef(null); //useRef       .
  const [loading, setLoading] = useState(false);
  const TOTAL_SLIDES = 12;

  const [soft, setSoft] = useState<number>(0);
  const [web, setWeb] = useState<number>(0);
  const [design, setDesign] = useState<number>(0);

  // const handleOnSelect = (e: MouseEvent<HTMLButtonElement>) => {
  //   const value = e.currentTarget.value;
  //   setUserClass(prev => [...prev, value]);
  //   setQ(prev => prev + 1);
  // };

  const nextSlideSec1 = () => {
    //  
    setUserClass(userClass + qnaData[q].a[0].value);
    setQ(q + 1);
    setCurrentSlide(currentSlide + 1);
    setSoft(soft + 1);
    console.log(soft);
  };

  const nextSlideSec2 = () => {
    //  
    setUserClass(userClass + qnaData[q].a[1].value);
    setQ(q + 1);
    setCurrentSlide(currentSlide + 1);
    setWeb(web + 1);
    console.log(web);
  };

  const nextSlideSec3 = (e: any) => {
    //  
    setUserClass(userClass + qnaData[q].a[2].value);
    setQ(q + 1);
    setCurrentSlide(currentSlide + 1);
    setDesign(design + 1);
    console.log(design);
  };

  const handleMoveToResult = () => {
    //1.     count[0]  ..    .
    //     ?
    //  let count[2] = ''   ..........?
    // setLoading(true);

    //
    //      =>  
    //      => 
    //      => 
    //    =>   5  => 
    //    => 
    //    =>    
    //   6 6  => 
    //   6 6   => 
    //   6 6 => 
    // 4 4 4   =>    

    // //   12 =>  
    if (soft == web && soft == design && web == design) {
      setTimeout(() => {
        navigate("/web");
      }, 3000);
      return <LoadingPage />;
    }

    //    =>  
    if (soft == design) {
      if (web == 0) {
        setTimeout(() => {
          navigate("/web");
        }, 3000);
        return <LoadingPage />;
      }
    }
    //    =>  
    if (soft == web) {
      if (design >= 0 && design < 2) {
        setTimeout(() => {
          navigate("/soft");
        }, 3000);
        return <LoadingPage />;
      }
    }
    //    =>  
    if (web == design) {
      if (soft >= 0 && soft < 2) {
        setTimeout(() => {
          navigate("/design");
        }, 3000);
        return <LoadingPage />;
      }
    }
    //    =>  
    if (soft > web) {
      //    =>  
      if (soft > design) {
        setTimeout(() => {
          navigate("/soft");
        }, 3000);
        return <LoadingPage />;
      }
    }

    //    =>  
    if (web > soft) {
      //    =>  
      if (web > design) {
        setTimeout(() => {
          navigate("/web");
        }, 3000);
        return <LoadingPage />;
      }
    }

    //    =>  
    if (design > soft) {
      //    =>  
      if (design > web) {
        setTimeout(() => {
          navigate("/design");
        }, 3000);
        return <LoadingPage />;
      }
    }
    return 0;
  };

  if (!qnaData[q]) return <>{handleMoveToResult()}</>;

  return (
    <>
      <S.QnaWrapper>
        <MainContainer>
          <MainWhiteBox></MainWhiteBox>
          <S.CountWrapper
            style={
              qnaData[q].id <= 9 ? {marginLeft: "73.5%"} : {marginLeft: "72.8%"}
            }
          >
            <span className="count_q">{qnaData[q].id}</span>
            <span className="total_q"> /{TOTAL_SLIDES}</span>
          </S.CountWrapper>
          <S.TitleContainer>
            {/* {qnaData.map((q: any, id: number) => (
            <S.Title key={q.id}>{qnaData[0].q}</S.Title>
          ))} */}

            <S.Title>{qnaData[q].q}</S.Title>
          </S.TitleContainer>
          <S.Hidden>
            <span>{qnaData[q].hidden}</span>
          </S.Hidden>
          <S.QnaItemContainer>
            <S.QnaItem onClick={nextSlideSec1} value={qnaData[q].a[0].value}>
              {qnaData[q].a[0].answer}
            </S.QnaItem>
            <S.QnaItem onClick={nextSlideSec2} value={qnaData[q].a[1].value}>
              {qnaData[q].a[1].answer}
            </S.QnaItem>
            <S.QnaItem onClick={nextSlideSec3} value={qnaData[q].a[2].value}>
              {qnaData[q].a[2].answer}
            </S.QnaItem>
          </S.QnaItemContainer>
        </MainContainer>
      </S.QnaWrapper>
    </>
  );
};

export default QnaPage;
