import React from "react";
import {Link} from "react-router-dom";
import {useEffect} from "react";
import {ToastSuccess, ToastError} from "../../lib/hook/toastHook";
import * as S from "./style";

declare global {
  interface Window {
    Kakao: any;
  }
}

const WebPage = () => {
  const handleCopyClipBoard = async (text: string) => {
    try {
      await navigator.clipboard.writeText(text);

      ToastSuccess(" !");
    } catch (error) {
      ToastError(" !");
    }
  };

  // const kakao = (window as any).kakao;

  useEffect(() => {
    console.log("kakao init");
    if (!window.Kakao.isInitialized()) {
      window.Kakao.init(process.env.REACT_APP_KAKAO_KEY);
      console.log(window.Kakao.isInitialized());
    }
  }, []);

  const kakaoShare = () => {
    window.Kakao.Link.sendDefault({
      objectType: "feed",
      content: {
        title: "",
        description: "     ",
        imageUrl: `${process.env.PUBLIC_URL}/result-img/main.jpeg`,
        link: {
          mobileWebUrl: "https://mirimirim-936ae.web.app",
          webUrl: "https://mirimirim-936ae.web.app",
        },
      },
      buttons: [
        {
          title: " ",
          link: {
            mobileWebUrl: "https://mirimirim-936ae.web.app",
            webUrl: "https://mirimirim-936ae.web.app",
          },
        },
      ],
    });
  };

  return (
    <>
      <S.MainWrapper>
        <S.Img src="../result-img/web.png" id="img1" />
        <S.Developer1></S.Developer1>
        <S.Developer2></S.Developer2>
        <S.Developer3></S.Developer3>
        <S.Developer4></S.Developer4>
        <S.Developer5></S.Developer5>

        <S.Share onClick={kakaoShare} style={{cursor: "pointer"}}>
          <img src="../result-img/share-white.png" id="img3" />
        </S.Share>

        <S.LinkBtn
          onClick={() => handleCopyClipBoard(" ")}
          style={{cursor: "pointer"}}
        >
          <img src="../result-img/link-white.png" id="img4" />
        </S.LinkBtn>

        <Link to="/" style={{textDecoration: "none"}}>
          <S.Home style={{cursor: "pointer"}}>
            <img src="../result-img/home-white.png" id="img2" />
          </S.Home>
        </Link>
      </S.MainWrapper>
    </>
  );
};

export default WebPage;
